export function fetchPostsApi() {
    var num = parseInt(Math.random() * 5, 10)
    return axios.post('/findAllSystemAnnouncement', {
        pageNum: num,
        pageSize: 12,
        currentPage: num
    }).then(function (result) {
        console.log(result)
        return result.data.list;
    })
}


